

.side-nav-container {
  width: 20%;
  height:100vh;
  background-color:#333333;
  overflow-y: scroll;
  padding-left: 15px;
  padding-top: 50px;

}

.side-nav-container a {
  text-decoration: none;
  color: white;
  font-family: Source Sans Pro,Roboto,Open Sans,Liberation Sans,DejaVu Sans,Verdana,Helvetica,Arial,sans-serif;
}

.logo-container {
  display:flex;
  justify-content: space-evenly;
  width:100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.logo {
  color: white;
}
.side-nav-container li {
  list-style-type: none;
  font-size: x-large;
  margin-bottom: 5px;
}


